
.boxshadow {
    display: flex;
    max-width: 1540px;
    margin: 0 auto;
    padding: 20px;
}

.boxshadow .option {
    flex: 1;
    border: 1px solid #ccc;
    border-radius: 20px;
    padding-top: 0;
    overflow: hidden;
}

.boxshadow .option .title {
    padding-top: 0;
    text-align: center;
    border-bottom: 1px solid #ccc;
    height: 60px;
    background-color: black;
    color: white;
}
.boxshadow .option .title h2 {
    margin: 0;
    line-height: 60px;
}

.boxshadow .option .option_row {
    padding: 10px 20px;
    border-bottom: 1px solid #ccc;
    height: 70px;
}

.boxshadow .option .option_row.last_row {
    border-bottom: none;
}

.boxshadow .option .option_row .option_title {
    margin-bottom: 10px;
    margin-top: 5px;
    display: flex;
    justify-content: space-between;
}

.boxshadow .option .option_row .option_body {
    display: flex;
    flex-direction: column;
    align-items: end;
}

.boxshadow .option .option_row .option_title span {
    color: rgb(13, 87, 87);
    font-weight: 600;
}

.boxshadow .result {
    flex: 3;
    padding: 10px;
}

.boxshadow .result .result_box_container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 450px;
}

.boxshadow .result .result_box {
    width: 500px;
    height: 300px;
    z-index: 100;
}

.boxshadow .result .result_text {
    width: 500px;
    padding: 30px;
    border-radius: 10px;
    margin: 0 auto;
    border: 1px solid #ccc;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.boxshadow .result .result_text .result_text_copy {
    width: 80px;
    margin: auto;
}